.mainWrapper {
  display: flex;
  flex-direction: column;
  gap: 56rem;
  padding: 40rem 24rem;
  max-width: 232rem;
  min-width: 150rem;
  width: 18.5%;
  flex-shrink: 0;
  max-height: 100%;
  overflow-y: auto;
  background: #ffffff;
  box-shadow: 0rem 0rem 4rem rgba(0, 0, 0, 0.12);
  border-radius: 2rem;
}

.userInfoWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.imageWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 112rem;
  height: 112rem;
  border: 1.2rem solid #001c54;
  border-radius: 50%;
  margin-bottom: 16rem;
}

.avatar {
  display: block;
  width: 96rem;
  height: 96rem;
  border-radius: 50%;
  object-fit: cover;
}

.userName {
  font-size: 16rem;
  line-height: 19rem;
  color: #000000;
  margin-bottom: 4rem;
  text-align: center;
}

.eduProgress {
  font-size: 12rem;
  line-height: 15rem;
  color: rgba(0, 0, 0, 0.5);
  text-align: center;
}

.eduInfoList {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 26rem;
  text-align: center;
}

.eduInfoItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;

  font-size: 14rem;
  line-height: 20rem;
  color: #000000;
}

.eduInfoProperty {
    font-weight: 400;
}

.eduInfoValue {
    font-weight: 600;
}
